<template>
  <div id="parent">
    <div class="item">
      <div class="value">
        <h1>カテゴリー</h1>
      </div>
      <div class="dropper">
        <v-select :options="options"></v-select>
      </div>
    </div>
    <div class="item">
      <div class="value">
        <h1>アクセスポイント</h1>
      </div>
      <textarea v-model="message" placeholder="アピールポイントを記入してください。" cols="50"></textarea>
    </div>
    <br />
    <div class="item">
      <div class="value">
        <h1>写真のアップロード</h1>
      </div>
      <croppa v-model="Croppa" canvas-color="#F2F2F2"></croppa>
    </div>
  </div>
</template>
 
<script>
import Vue from "vue";
import Croppa from "vue-croppa";
import vSelect from "vue-select";
import "vue-select/dist/vue-select.css";
import { Stream } from "stream";

Vue.component("v-select", vSelect);
Vue.use(Croppa);

export default {
  name: "#app",
  data() {
    return {
      Croppa: null,
      options: ["スポーツ", "グルメ", "アクティビティ", "風景", "その他"]
    };
  },
  methods: {
    generateImage: function() {
      let url = this.Croppa.generateDataUrl();
      if (!url) {
        alert("no image");
        return;
      }
    }
  }
};
</script>
 
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
body {
  font-family: "Source Sans Pro", "Helvetica Neue", Arial, sans-serif;
  text-rendering: optimizelegibility;
  -moz-osx-font-smoothing: grayscale;
  -moz-text-size-adjust: none;
}

h1,
h2,
h3,
.muted {
  color: #2c3e5099;
}

h1 {
  font-size: 20px;
  font-weight: 600;
}

#parent {
  margin-top: 40px;
  margin-left: 20px;
}

.item {
  display: flex;
  margin-top: 20px;
}

.value {
  display: flex;
  width: 200px;
  height: 60px;
  margin-right: 50px;
}

.dropper {
  width: 200px;
}

h3 {
  margin: 40px 0 0;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>


